* {
  margin: 0;
  padding: 0;
  background: #17212f;
  background-position: 100% 100%;
  background-size: auto 75%;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  margin-top: 4rem;
}

.logo-header {
  color: white;
  /* margin-top: 4rem; */
}

.list-header {
  display: flex;
  align-items: flex-end;
  color: white;
}
.list-header ul {
  display: flex;
}

.list-header ul li {
  list-style: none;
  margin-right: 4rem;
}

.main-section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding-top: 10rem;
  padding-bottom: 10rem;
  padding-left: 13rem;
  width: 100%;
}
@media (max-width: 768px) {
  .main-section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding-top: 1rem;
    padding-bottom: 10rem;
    padding-left: 2rem;
    width: 100%;
  }
}

@media (min-width: 768px) {
  .section-1 {
    font-size: 3.75rem;
    line-height: 1;
  }
}

.section-1 {
  width: 50%;
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
  font-weight: 700;
  display: block;
}
.section-1 {
  font-size: 3.75rem;
  line-height: 1;
}
.section-1 span {
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  --tw-gradient-to: #7291ff;

  --tw-gradient-from: #f25533;
  --tw-gradient-stops: var(--tw-gradient-from),
    var(--tw-gradient-to, rgba(242, 85, 51, 0));

  background-image: linear-gradient(to right, var(--tw-gradient-stops));
}
@media (max-width: 768px) {
  .section-1 {
    font-size: 2rem;
    line-height: 1.25;
  }
}

.section-1 p {
  margin-top: 2rem;
  font-size: 1.25rem;
  line-height: 1.75rem;
  width: 80%;
  font-weight: 300;
}

.section-2 {
  color: white;
}

.playground-button {
  z-index: 100;
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, 1);
  font-weight: 700;
  font-size: 0.875rem;
  line-height: 1.25rem;
  text-align: center;
  background-color: rgba(242, 85, 51, 1);
  border-color: transparent;
  border-width: 1px;
  border-radius: 0.375rem;
  margin-right: 2rem;
  padding: 0.5rem 0.5rem;
  opacity: 0.9;
  cursor: pointer;
}

.cta {
  display: flex;
  flex-direction: row;
  margin-top: 2rem;
}

@media (max-width: 768px) {
  .section-2 {
    display: none;
  }
}
.section-2 {
  width: 50%;
}

@media (max-width: 768px) {
  .section-1 {
    width: 90%;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    font-weight: 700;
    display: block;
  }
}
@media (max-width: 768px) {
  .list-header {
    display: none;
  }
}
